import React, { Component } from "react";
import { Field, formValueSelector, reduxForm } from "redux-form";
import { connect } from "react-redux";
import Axios from "axios";
import { validationMutasiBarang } from "../../Validations/validationMutasiBarang";

const server = process.env.REACT_APP_BACKEND_URL;
const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      min="0"
      step="0.001"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);
class FormMutasiBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listBarang: [],
      kode_asal: "",
      nama_asal: "",
      kadar_asal: "",
      pkg_asal: "",
      kotor_asal: "",
      harga_asal: "",
      murni_asal: "",
      kode_tujuan: "",
      nama_tujuan: "",
      kadar_tujuan: "",
      pkg_tujuan: "",
      kotor_tujuan: "",
      harga_tujuan: "",
      murni_tujuan: "",
    };
  }

  componentDidMount() {
    Axios.get(server + "subcategorys2?kodeKelompok=PERHIASAN")
      .then((res) =>
        this.setState({
          listBarang: res.data,
        })
      )
      .catch((res) => console.log("ERROR GET DATA"));
  }

  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  submit = () => {
    console.log(JSON.stringify(this.state));
  };

  handleChange = (input) => (e) => {
    var supp = e.target.value
      ? e.target.value
      : "DEFAULT | DEFAULT | DEFAULT | DEFAULT ";
    var result_explode = supp.split("|");
    this.setState({
      kodeJenis: result_explode[0],
      kadarCetak: result_explode[1],
      kadarBeli: result_explode[2],
      namaJenis: result_explode[3],
    });
    this.props.change("kadar", result_explode[1]);
    this.props.change("kadarTransaksi", result_explode[2]);
    this.props.change("keterangan", result_explode[3]);
  };
  handleChangeTujuan = (input) => (e) => {
    var supp = e.target.value
      ? e.target.value
      : "DEFAULT | DEFAULT | DEFAULT | DEFAULT ";
    var result_explode = supp.split("|");
    this.setState({
      kodeJenisTujuan: result_explode[0],
      kadarCetakTujuan: result_explode[1],
      kadarBeliTujuan: result_explode[2],
      namaJenisTujuan: result_explode[3],
    });
    this.props.change("kadarTujuan", result_explode[1]);
    this.props.change("kadarTransaksiTujuan", result_explode[2]);
    this.props.change("keteranganTujuan", result_explode[3]);
  };
  setKadar() {
    this.props.change("kadar", this.state.kadarCetak);
    this.props.change("kadarTujuan", this.state.kadarCetakTujuan);
    this.props.change("kadarTransaksi", this.state.kadarBeli);
    this.props.change("kadarTransaksiTujuan", this.state.kadarBeliTujuan);
    this.props.change("keterangan", this.state.namaJenis);
    this.props.change("keteranganTujuan", this.state.namaJenisTujuan);
  }
  setNetto = (e) => {
    this.props.change(
      "netto",
      this.props.netto === "NaN" ? 0 : this.props.netto
    );
    this.props.change(
      "nettoTujuan",
      this.props.nettoTujuan === "NaN" ? 0 : this.props.nettoTujuan
    );
  };
  setPkg = (input) => (e) => {
    this.props.change("pkgTujuan", e.target.value);
  };
  setBruto = (input) => (e) => {
    this.props.change("brutoTujuan", e.target.value);
  };
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-md-3 form-group">
            <label htmlFor="">Kode Asal</label>
            <Field
              name="kodeBarang"
              component="select"
              className="form-control"
              onChange={this.handleChange()}
              onBlur={() => this.setKadar()}
            >
              <option value="DEFAULT|DEFAULT|DEFAULT" key="DEFAULT">
                SILAHKAN PILIH
              </option>
              {this.state.listBarang.map((list) => (
                <option
                  value={
                    list.kodeJenis +
                    "|" +
                    list.kadarCetak +
                    "|" +
                    list.kadarJual +
                    "|" +
                    list.namaJenis
                  }
                  key={list.kodeJenis}
                >
                  {list.namaJenis}
                </option>
              ))}
            </Field>
          </div>
          <div className="col-md-4">
            <Field
              name="keterangan"
              label="Keterangan"
              component={renderField}
              type="text"
            />
          </div>
          <div className="col-md-4 col-lg-4"></div>
          <div className="col-md-3 mt-1">
            <Field
              name="kadar"
              label="Kadar (%)"
              component={renderField}
              type="text"
            />
          </div>
          <div className="col-md-3 mt-1">
            <Field
              name="pkg"
              label="Pkg (Gr)"
              component={renderField}
              type="text"
              onBlur={() => this.setNetto()}
              onChange={this.setPkg()}
            />
          </div>
          <div className="col-md-3 mt-1">
            <Field
              name="bruto"
              label="Bruto (Gr)"
              component={renderField}
              type="text"
              onBlur={() => this.setNetto()}
              onChange={this.setBruto()}
            />
          </div>
          <div className="col-md-3 mt-1">
            <Field
              name="kadarTransaksi"
              label="Harga (Gr)"
              component={renderField}
              type="text"
              readOnly={true}
            />
          </div>
          <div className="col-md-3 mt-1">
            <Field
              name="netto"
              label="Netto (Gr)"
              component={renderField}
              type="text"
            />
          </div>
          <div className="col-lg-12 col-md-12 text-center mt-2 mb-2">
            <div style={{ transform: "rotate(90deg)", fontSize: 24 }}>
              <i className="fa fa-exchange-alt"></i>
            </div>
          </div>
          <div className="col-md-3 form-group">
            <label htmlFor="">Kode Tujuan</label>
            <Field
              name="kodeBarangTujuan"
              component="select"
              className="form-control"
              onChange={this.handleChangeTujuan()}
              onBlur={() => this.setKadar()}
            >
              <option value="DEFAULT|DEFAULT|DEFAULT" key="DEFAULT">
                SILAHKAN PILIH
              </option>
              {this.state.listBarang.map((list) => (
                <option
                  value={
                    list.kodeJenis +
                    "|" +
                    list.kadarCetak +
                    "|" +
                    list.kadarBeli +
                    "|" +
                    list.namaJenis
                  }
                  key={list.kodeJenis}
                >
                  {list.namaJenis}
                </option>
              ))}
            </Field>
          </div>
          <div className="col-md-4">
            <Field
              name="keteranganTujuan"
              label="Keterangan"
              component={renderField}
              type="text"
              readOnly={true}
            />
          </div>
          <div className="col-md-4 col-lg-4"></div>
          <div className="col-md-3 mt-1">
            <Field
              name="kadarTujuan"
              label="Kadar (%)"
              component={renderField}
              type="text"
              readOnly={true}
            />
          </div>
          <div className="col-md-3 mt-1">
            <Field
              name="pkgTujuan"
              label="Pkg (Gr)"
              component={renderField}
              type="text"
              onBlur={() => this.setNetto()}
              readOnly={true}
            />
          </div>
          <div className="col-md-3 mt-1">
            <Field
              name="brutoTujuan"
              label="Bruto (Gr)"
              component={renderField}
              type="text"
              onBlur={() => this.setNetto()}
              readOnly={true}
            />
          </div>
          <div className="col-md-3 mt-1">
            <Field
              name="kadarTransaksiTujuan"
              label="Harga (Gr)"
              component={renderField}
              type="text"
              onBlur={this.setNetto()}
              onChange={() => this.setNetto()}
            />
          </div>
          <div className="col-md-3 mt-1">
            <Field
              name="nettoTujuan"
              label="Netto (Gr)"
              component={renderField}
              type="text"
              readOnly={true}
            />
          </div>
          <div className="col-lg-12 col-md-12">
            <div className="text-right">
              <button className="btn btn-primary" onClick={this.submit}>
                Simpan
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormMutasiBarang = reduxForm({
  form: "FormMutasiBarang",
  enableReinitialize: true,
  validate: validationMutasiBarang,
})(FormMutasiBarang);
const selector = formValueSelector("FormMutasiBarang"); // <-- same as form name
export default connect((state) => {
  const { bruto, kadarTransaksi, kadarTransaksiTujuan } = selector(
    state,
    "bruto",
    "kadarTransaksi",
    "kadarTransaksiTujuan"
  );
  return {
    netto: (parseFloat(bruto) * (parseFloat(kadarTransaksi) / 100)).toFixed(3),
    nettoTujuan: (
      parseFloat(bruto) *
      (parseFloat(kadarTransaksiTujuan) / 100)
    ).toFixed(3),
  };
})(FormMutasiBarang);
